<template>
  <div>
    <p>Dodo Pizza Engineering:</p>
    <a
      v-for="(link, index) of links"
      v-text="link.title"
      :key="index"
      :href="link.href"
      target="_blank"
      class="button button-inline"
      @click="sendMetrika(link.eventId)"
    ></a>
    <br />
  </div>
</template>

<script>
import LinksList from "../content/links";
export default {
  data: () => ({
    links: [...Object.values(LinksList)].filter(l => l.title)
  }),

  methods: {
    sendMetrika(eventId) {
      window.ym.send(eventId, this.$store.devmode ? "dev" : "manager");
    }
  }
};
</script>

