<template>
  <div>
    <a
      v-for="job in jobs"
      :key="job.id"
      :href="job.application_url"
      target="_blank"
      class="button button-outer job"
      @click="sendMetrika(job)"
    >{{job.full_title}}</a>
  </div>
</template>

<script>
export default {
  data: () => ({
    jobs: [ ...window.jobs ]
  }),

  methods: {
    sendMetrika(job) {
      window.ym.send(job.yandexEventId, this.$store.devmode ? 'dev' : 'manager');
    },

    showItem(items, i) {
      const item = items[i];
      return function () {
        item.classList.toggle('job-show');
      }
    }
  },

  mounted() {
    const items = document.getElementsByClassName('job');
    var delay = 1;

    for (var i = 0; i < items.length; i++ ) {
      delay = i * 50;
      setTimeout(this.showItem(items, i), delay);
    }
  }
};
</script>

<style lang="scss">
  .job {
    display: none;

    &-show {
      display: block;
    }
  }
</style>